<template>
  <article v-if="!isLoading">
    <header class="header card">
      {{fixture.location.title}}
      ({{position.lat}}, {{position.lng}})
    </header>
    <section>
    <div class="card" v-for="(value,key, index) in model"><!--  @click="$router.push(`/restaurant/${value.restaurant.id}`)"> -->
      <img width="100" :src="value.restaurant.thumb" alt="">
      <div class="card-content">
          <h1>{{value.restaurant.name}}</h1>
          <address><small>{{value.restaurant.location.locality}}</small></address>
      </div>
    </div>
    </section>
  </article>
  <ActivityIndicator v-else />
</template>

<script>
import fixture from "./fixture.json";
export default {
  name: "Index",
  created() {
    this.getPosition();
  },
  data() {
    return {
      fixture,
      position: {
        lat: 0,
        lng: 0
      }
    };
  },
  methods: {
    getPosition() {
      var options = {
        enableHighAccuracy: true,
        timeout: 5000,
        maximumAge: 0
      };
      var vm = this;
      function success(pos) {
        var crd = pos.coords;
        vm.position.lat = crd.latitude;
        vm.position.lng = crd.longitude;
      }

      function error(err) {
        console.warn(`ERROR(${err.code}): ${err.message}`);
      }

      navigator.geolocation.getCurrentPosition(success, error, options);
    }
  },
  computed: {
    isLoading() {
      return this.position.lat === 0 ? true : false;
    },
    model() {
      return this.fixture.nearby_restaurants.sort((a, b) => {
        if (a.restaurant.name < b.restaurant.name) {
          return -1;
        } else if (a.restaurant.name > b.restaurant.name) {
          return 1;
        } else {
          return 0;
        }
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.card-content {
  padding: 1rem;
}
.card {
  height: 100px;
}

.header {
  width: 100% !important;
  margin: 0;
  padding: 1rem;
  border-radius: 0;
  height: inherit;
}
</style>



